@import "./variables.scss";
@import "./breakpoints.scss";

.admin-listing-page {
    padding: 20px;
    background-color: #f4f4f9;
    min-height: 100vh;

    h2 {
        text-align: center;
        font-size: 2rem;
        margin-bottom: 20px;
        color: #333;
        font-family: nunito, sans-serif;
    }

    table {
        width: 100%;
        border-collapse: collapse;
        margin: 20px 0;
        background-color: white;
        box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
        border-radius: 8px;

        th, td {
            padding: 15px;
            text-align: left;
            font-size: 1rem;
            font-family: nunito, sans-serif;
        }

        th {
            background-color: $blue;
            color: white;
            font-weight: bold;
            font-family: nunito, sans-serif;
        }

        tr:nth-child(even) {
            background-color: #f9f9f9;
        }

        tr:hover {
            background-color: #e1e1e1;
        }
    }

    button {
        background-color: #28a745;
        color: white;
        border: none;
        padding: 8px 16px;
        font-size: 1rem;
        border-radius: 5px;
        cursor: pointer;

        &:hover {
            background-color: #218838;
        }

        &:disabled {
            background-color: #ccc;
            cursor: not-allowed;
        }
    }

    a {
        color: $blue;
        text-decoration: none;
        font-size: 1rem;

        &:hover {
            text-decoration: underline;
        }
    }

    .error-message {
        color: red;
        text-align: center;
        font-size: 1.2rem;
        margin-top: 20px;
    }

    .loading-message {
        text-align: center;
        font-size: 1.2rem;
        color: $darkgrey;
        margin-top: 20px;
    }
}
