@import "./breakpoints.scss";
@import "./variables.scss";

.admin-page {
  font-family: 'Nunito', sans-serif; // Set font for the entire admin page

  h1, h2 {
    color: $blue; // Use blue for headers
    margin-bottom: 15px;
  }

  table {
    width: 80%; // Set the width to 80% of its container
    max-width: 1200px; // Set a max-width to prevent the table from becoming too large
    border-collapse: collapse; // Combine borders
    margin-bottom: 20px;

    th, td {
      border: 1px solid $blue; // Use blue for table borders
      padding: 8px 12px; // Slightly reduce padding to make the table more compact
      text-align: left;

      &:first-child {
        width: 20%; // Set width for first column (Booking/User ID)
      }
    }

    th {
      background-color: lighten($blue, 40%); // Lighten blue for header background
      color: darken($blue, 20%); // Darken blue for header text
    }

    tr:nth-child(even) {
      background-color: lighten($blue, 80%); // Lighten blue for even rows
    }
  }

  button {
    background-color: $blue; // Button background color
    color: white; // Button text color
    border: none;
    padding: 10px 15px;
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s;

    &:hover {
      background-color: darken($blue, 10%); // Darken button on hover
    }
  }

  // Chart styles
  .statistics {
    margin-top: 20px;
  }

  .chart-container {
    width: 80%;         // Set the width of the chart (adjust as needed)
    max-width: 600px;   // Limit the max width to prevent it from getting too wide
    margin-left: 40px;     // Center the chart horizontally
    padding: 20px;      // Add some padding around the chart
  }

  .chart-container canvas {
    width: 100% !important; // Ensure the canvas takes up the full width of its container
    height: 400px !important; // Set the height for the chart (adjust as needed)
  }

  .align-left {
    margin-left: 40px;
  }

  .listing-titles-popup {
    position: absolute; /* Position relative to the parent cell */
    top: 50%; /* Vertically align near the cell */
    left: 105%; /* Position it to the right of the cell */
    transform: translateY(-50%); /* Adjust for centering */
    background-color: #fff;
    border: 1px solid #ccc;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    padding: 10px;
    z-index: 100; /* Ensure it appears above other elements */
    border-radius: 5px;
    width: 400px; /* Adjust width as needed */
  }
  
  .listing-title-box {
    background-color: #f4f4f4;
    margin: 5px 0;
    padding: 8px;
    border-radius: 4px;
    font-size: 14px;
  }

}
