@import "./breakpoints.scss";
@import "./variables.scss";

.listing-card {
  position: relative; /* Set relative positioning for the listing card */
  margin: 1rem; /* Add spacing between cards if needed */
}

.title-list {
  margin: 40px 100px;
  color: $blue;
  font-family: nunito, sans-serif;

  @include sm {
    margin: 40px 50px;
  }
}

.list {
  padding: 0 100px 120px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 25px;

  @include xl {
    padding: 0 20px 120px;
  }
}

.trip-card {
  position: relative;
  .delete-btn {
    position: absolute;
    top: 10px;
    right: 10px;
    background-color: transparent;
    border: none;
    cursor: pointer;
    font-size: 1.5rem;
    color: red;
    transition: color 0.2s ease-in-out;

    &:hover {
      color: darken(red, 10%);
    }

    svg {
      width: 20px;
      height: 20px;
    }
  }
}

.delete-button {
  position: absolute; /* Position it absolutely within the card */
  top: 10px; /* Adjust vertical positioning */
  right: 10px; /* Adjust horizontal positioning */
  background: transparent;
  border: none;
  cursor: pointer;
  color: red; /* Change color as needed */
  font-size: 1.5rem; /* Adjust size as needed */
  transition: color 0.3s;

  &:hover {
    color: darkred; /* Change color on hover */
  }
}
