@import "./variables.scss";
@import "./breakpoints.scss";

.about-us-container {
  padding: 10px 140px 140px;
  font-family: nunito, sans-serif;

  @include xl {
    padding: 40px 80px 120px;
  }

  @include lg {
    padding: 40px 50px 120px;
  }

  @include sm {
    padding: 40px 20px 120px;
  }

  .title {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include sm {
      flex-direction: column;
      align-items: flex-start;
      gap: 15px;
    }

    h1 {
      font-size: 36px;
      @include sm {
        font-size: 28px;
        color: $blue;
      }
    }
  }

  .about-content {
    margin-top: 20px;
    max-width: 800px;

    p {
      font-size: 18px;
      line-height: 1.6;
      margin-bottom: 20px;
    }
  }

  .team {
    margin-top: 40px;

    h2 {
      font-size: 28px;
      margin-bottom: 20px;
      border-bottom: 2px solid #ccc;  // Horizontal line under h2
      padding-bottom: 10px;  // Space between heading and line
      color: $blue;
    }

    .team-members {
      display: flex;
      gap: 30px;
      flex-wrap: wrap;

      .member {
        display: flex;
        flex-direction: column;
        align-items: center;

        img {
          width: 100px;
          height: 100px;
          border-radius: 50%;
          margin-bottom: 15px;
        }

        .name {
          font-size: 20px;
          font-weight: bold;
        }

        .role {
          font-size: 16px;
          color: #888;
        }
      }
    }
  }

  .contact {
    margin-top: 40px;
    background-color: #f8f8f8;
    padding: 20px;
    border-radius: 8px;

    h3 {
      font-size: 24px;
      margin-bottom: 20px;
    }

    .contact-info {
      display: flex;
      gap: 20px;

      .contact-item {
        display: flex;
        align-items: center;
        gap: 10px;

        .icon {
          font-size: 30px;
        }

        .info {
          font-size: 18px;
        }
      }
    }
  }

  // Add space between the headings and content
  h2, h3, h1 {
    margin-top: 40px; // Space before the heading
  }

  p, ul {
    margin-top: 15px; // Space after the heading
  }
}
