@import "./variables.scss";
@import "./breakpoints.scss";

.payment-success-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  text-align: center;
  max-width: 800px;
  margin: 0 auto;
  font-family: nunito, sans-serif;
  
  h1 {
    font-size: 2.5rem;
    color: $blue;
    margin-bottom: 1rem;
  }

  p {
    font-size: 1.2rem;
    color: #333;
    margin-bottom: 2rem;
  }

  .payment-details-table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 2rem;

    th,
    td {
      padding: 1rem;
      text-align: left;
      border: 1px solid #ddd;
    }

    th {
      background-color: #f4f4f4;
      font-weight: bold;
    }

    td {
      font-size: 1rem;
    }
  }

  .back-to-home-btn {
    background-color: $blue;
    color: #fff;
    border: none;
    padding: 1rem 2rem;
    font-size: 1rem;
    cursor: pointer;
    border-radius: 5px;
    text-align: center;
    transition: background-color 0.3s;

    &:hover {
      background-color: $pinkred;
    }
  }

  // Mobile responsiveness
  @media (max-width: 600px) {
    h1 {
      font-size: 2rem;
    }

    p {
      font-size: 1rem;
    }

    .payment-details-table {
      th, td {
        padding: 0.8rem;
      }
    }

    .back-to-home-btn {
      font-size: 0.9rem;
      padding: 0.8rem 1.5rem;
    }
  }
}
