@import "./variables.scss";
@import "./breakpoints.scss";

.contact-support-container {
    padding: 20px;
    max-width: 800px;
    margin: 0 auto;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    font-family: nunito, sans-serif;
  }
  
  h1 {
    text-align: center;
    color: black;
  }
  
  p {
    text-align: center;
    color: #555;
  }
  
  .form-group {
    margin-bottom: 15px;
  }
  
  label {
    font-size: 14px;
    color: #555;
  }
  
  input, textarea {
    width: 100%;
    padding: 10px;
    margin-top: 5px;
    border: 1px solid #ddd;
    border-radius: 4px;
  }
  
  textarea {
    resize: vertical;
    height: 150px;
  }
  
  button.submit-btn {
    width: 100%;
    padding: 12px;
    background-color: $blue;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
  }
  
  button.submit-btn:hover {
    background-color: $pinkred;
  }
  
  .success-message {
    color: green;
    text-align: center;
  }
  
  .error-message {
    color: red;
    text-align: center;
  }
  
  ul {
    padding: 0;
    list-style-type: none;
    margin-top: 20px;
  }
  
  ul li {
    font-size: 16px;
    color: #555;
    margin: 5px 0;
  }
  