@import "./breakpoints.scss";
@import "./variables.scss";

.leave-review-page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;  // Ensure the content stretches to fill the screen
}

.leave-review-container {
  flex: 1;  // Take up available space between the Navbar and Footer
  padding: 40px 100px;
  display: flex;
  flex-direction: column;
  align-items: center;

  @include sm {
    padding: 40px 50px;
  }

  h2 {
    color: $blue;
    font-family: nunito, sans-serif;
    font-size: 24px;
    text-align: center;
    margin-bottom: 20px;
  }

  form {
    width: 100%;
    max-width: 800px;
    display: flex;
    flex-direction: column;
    gap: 20px;

    .form-group {
      display: flex;
      flex-direction: column;
      gap: 10px;
      width: 100%;

      label {
        font-family: nunito, sans-serif;
        font-size: 16px;
        color: $blue;
      }

      select, textarea {
        width: 100%;
        padding: 12px 20px;
        font-size: 16px;
        border: 1px solid $grey;
        border-radius: 8px;
        background-color: transparent;
        outline: none;
        color: black;

        &:focus {
          border-color: $blue;
        }

        &::placeholder {
          color: $lightgrey;
        }
      }

      textarea {
        min-height: 120px;
        resize: vertical;
      }
    }

    .star-rating {
      display: flex;
      gap: 10px;
      font-size: 32px;  // Increase the font size for better visibility
      color: black;

      label {
        cursor: pointer;
        transition: color 0.2s;

        &.selected {
          color: gold;  // Highlight selected stars in gold
        }

        input {
          display: none;  // Hide the radio button inputs
        }
      }
    }

    button {
      margin-top: 20px;
      padding: 12px 20px;
      background-color: $blue;
      color: white;
      border: none;
      border-radius: 8px;
      font-size: 16px;
      cursor: pointer;
      width: 100%;

      &:hover {
        background-color: darken($blue, 10%);
        box-shadow: 0 0 10px 3px rgba(255, 255, 255, 0.3);
      }
    }
  }

  p {
    font-family: nunito, sans-serif;
    font-size: 18px;
    color: $blue;
  }
}
