@import "./breakpoints.scss";
@import "./variables.scss";

.title-profile {
  margin: 40px 100px;
  color: $blue;
  font-family: nunito, sans-serif;

  @include sm {
    margin: 40px 50px;
  }
}

.profile {
  padding: 0 100px;
  display: flex;
  flex-direction: column;
  align-items: center;

  .profile-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }

  .profile-section {
    display: flex;
    align-items: center;
    gap: 20px;
    flex-direction: row;
    margin-bottom: 20px; /* Space between image and details */
  }

  .profile-image {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    object-fit: cover;
  }

  .profile-details {
    display: flex;
    flex-direction: column;
    gap: 20px;
    width: 100%; /* Full width to align items below each other */
    align-items: flex-start; /* Aligns items to the left */
    
    label {
      font-family: nunito, sans-serif;
      font-size: 16px;
      color: $blue;
      width: 100%;
      margin-bottom: 5px;

      input {
        width: 100%;
        padding: 12px 20px;
        font-size: 16px;
        border: 1px solid $grey;
        border-radius: 8px;
        background-color: transparent;
        outline: none;
        color: black;

        &:focus {
          border-color: $blue;
        }

        &::placeholder {
          color: $lightgrey;
        }
      }
    }
  }

  .confirm-button{
    margin-top: 20px;
    padding: 10px 20px;
    background-color: $blue;
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 16px;
    cursor: pointer;
    width: 100%; /* Full width button */

    &:hover {
      background-color: darken($blue, 10%);
      box-shadow: 0 0 10px 3px rgba(255, 255, 255, 0.3);
    }
  }

  .request-deletion-button{
    margin-top: 20px;
    padding: 10px 20px;
    background-color: $blue;
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 16px;
    cursor: pointer;
    width: 40%; /* Full width button */

    &:hover {
      background-color: darken($blue, 10%);
      box-shadow: 0 0 10px 3px rgba(255, 255, 255, 0.3);
    }
  }
}
