@import "./variables.scss";
@import "./breakpoints.scss";
@import "~font-awesome/css/font-awesome.min.css";

.listing-details {
  padding: 40px 130px 120px;
  font-family: nunito, sans-serif;

  @include xl {
    padding: 40px 80px 120px;
  }

  @include lg {
    padding: 40px 50px 120px;
  }

  @include sm {
    padding: 40px 20px 120px;
  }

  .title {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include sm {
      flex-direction: column;
      align-items: flex-start;
      gap: 15px;
    }

    h1 {
      @include sm {
        font-size: 26px;
      }
    }

    .save {
      display: flex;
      align-items: center;
      gap: 10px;
      cursor: pointer;

      p {
        font-size: 20px;
        font-weight: 700;
        margin: 0;
      }
    }
  }

  .photos {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    margin: 20px 0;

    img {
      max-width: 280px;
    }
  }

  hr {
    margin: 20px 0;
  }

  .profile {
    display: flex;
    gap: 20px;
    align-items: center;

    img {
      width: 60px;
      height: 60px;
      margin: 0;
    }
  }

  p {
    max-width: 800px;
    margin-top: 20px;
  }

  .reviews {
    margin-top: 40px;

    h3 {
      font-size: 24px;
      font-weight: 600;
      margin-bottom: 15px;
    }

    .review-item {
      background-color: #f8f8f8;
      padding: 15px;
      margin-bottom: 20px;
      border-radius: 5px;
      display: flex;
      flex-direction: column;
      gap: 10px;

      .review-user {
        font-size: 18px;
        font-weight: 700;
      }

      .review-comment {
        font-size: 16px;
        color: #555;
      }

      .review-stars {
        display: flex;
        gap: 5px;
        font-size: 18px;
        color: #f1c40f;  // Gold color for stars
      }

      .review-stars .fa {
        color: #f1c40f;  // Apply gold color to stars
      }

      .review-stars .fa-star {
        color: #f1c40f;
      }

      .review-stars .fa-star-o {
        color: #ccc;
      }
    }
  }

  .booking {
    display: flex;
    justify-content: space-between;

    @include lg {
      flex-direction: column;
      gap: 50px;
    }

    .amenities {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-column-gap: 100px;
      margin: 30px 0;
      max-width: 700px;

      @include sm {
        grid-column-gap: 20px;
      }

      .facility {
        display: flex;
        align-items: center;
        gap: 20px;
        font-size: 18px;
        font-weight: 600;
        margin-bottom: 20px;

        &_icon {
          font-size: 30px;
        }

        p {
          margin: 0;
        }
      }
    }

    .date-range-calendar {
      margin: 30px 0;

      h2 {
        margin-bottom: 10px;
      }

      .button {
        @include buttonStyle;
        width: 100%;
        margin-top: 30px;

        @include lg {
          max-width: 300px;
        }
      }
    }
  }
}
