@import "../styles/variables.scss";
@import "../styles/breakpoints.scss";

.reviews-container {
  max-width: 900px;
  margin: 2rem auto;
  text-align: center;
  position: relative;
  font-family: nunito, sans-serif;

  h2 {
      margin-bottom: 1rem;
      font-size: 2rem;
      color: $blue;
  }

  .reviews-wrapper {
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;

      .reviews-carousel {
          display: flex;
          overflow: hidden;
          width: 100%;
          gap: 1rem;
          justify-content: center;

          .review-card {
              flex: 1 0 calc(50% - 1rem); // Two cards visible side-by-side
              max-width: 400px; // Max width for cards
              padding: 1.5rem;
              border-radius: 12px;
              background-color: #f9f9f9;
              box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
              text-align: center;
              display: flex;
              flex-direction: column;
              gap: 1rem;

              h3 {
                  font-size: 1.3rem;
                  color: $pinkred;
                  margin-bottom: 0.5rem;
              }

              p {
                  font-size: 1rem;
                  color: rgb(63, 62, 62);
                  line-height: 1.5;
                  margin: 0;
              }

              .stars {
                  color: #f8d32c;
                  font-size: 1.2rem;
              }

              .review-footer {
                  margin-top: 1rem;
                  font-size: 0.9rem;
                  color: $grey;
              }

              button {
                  padding: 0.5rem 1rem;
                  font-size: 1rem;
                  background-color: $blue;
                  color: #fff;
                  border: none;
                  border-radius: 6px;
                  cursor: pointer;
                  transition: background-color 0.3s;

                  &:hover {
                      background-color: $pinkred;
                  }
              }

              // Specific styles for the Edit button
              .edit-button {
                  align-self: flex-start;  // Align the edit button to the start of the card
                  padding: 0.5rem 1rem;
                  font-size: 1rem;
                  background-color: $blue;
                  color: #fff;
                  border: none;
                  border-radius: 6px;
                  cursor: pointer;
                  transition: background-color 0.3s;

                  &:hover {
                      background-color: $pinkred;
                  }
              }
          }
      }

      .arrow {
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          background-color: $blue;
          color: #fff;
          border: none;
          border-radius: 50%;
          font-size: 1.5rem;
          cursor: pointer;
          padding: 0.5rem;
          transition: background-color 0.3s;

          &:hover {
              background-color: $pinkred;
          }

          &.left {
              left: -2rem;
          }

          &.right {
              right: -2rem;
          }
      }
  }

  .edit-review-form {
      display: flex;
      flex-direction: column;
      margin-top: 1rem;
      gap: 1rem;

      textarea {
          width: 100%;
          padding: 1rem;
          border-radius: 6px;
          border: 1px solid #ccc;
          font-size: 1rem;
          resize: vertical;
      }

      .stars {
          display: flex;
          gap: 0.5rem;
          font-size: 1.5rem;
          color: #f8d32c;

          span {
              cursor: pointer;
          }

          .selected {
              color: gold;
          }
      }

      button {
          align-self: flex-start;
          padding: 0.5rem 1rem;
          font-size: 1rem;
          background-color: $blue;
          color: #fff;
          border: none;
          border-radius: 6px;
          cursor: pointer;
          transition: background-color 0.3s;

          &:hover {
              background-color: $pinkred;
          }

          .cancel-btn {
              background-color: $grey;
              &:hover {
                  background-color: $lightgrey;
              }
          }
      }
  }
}
