@import "./breakpoints.scss";
@import "./variables.scss";

.admin-reply-page {
    padding: 20px;
    background-color: #f4f6f9;
    min-height: 100vh;
  
    h2 {
      text-align: center;
      margin-bottom: 20px;
      font-family: 'Nunito', sans-serif;
      color: #333;
    }
  
    table {
      width: 100%;
      border-collapse: collapse;
      margin-top: 20px;
  
      th, td {
        padding: 15px;
        text-align: left;
        border-bottom: 1px solid #ddd;
        font-family: 'Nunito', sans-serif;
        color: #333;
      }
  
      th {
        background-color: $blue;
        color: white;
      }
  
      td {
        background-color: #fff;
      }
  
      tr:hover {
        background-color: #f1f1f1;
      }
  
      button {
        padding: 8px 16px;
        background-color: #28a745;
        color: white;
        border: none;
        border-radius: 5px;
        cursor: pointer;
        font-family: 'Nunito', sans-serif;
        transition: background-color 0.3s;
  
        &:hover {
          background-color: $pinkred;
        }
  
        &:disabled {
          background-color: #ccc;
          cursor: not-allowed;
        }
      }
  
      textarea {
        width: 100%;
        padding: 10px;
        border-radius: 5px;
        border: 1px solid #ccc;
        margin-top: 10px;
        resize: vertical;
        font-family: 'Nunito', sans-serif;
        min-height: 100px;
      }
  
      td span {
        color: #28a745;
        font-weight: bold;
      }
    }
  
    .loading-message {
      text-align: center;
      font-size: 1.2rem;
      color: #007bff;
    }
  
    .error-message {
      text-align: center;
      font-size: 1.2rem;
      color: #e74c3c;
    }
  }
  
  @media (max-width: 768px) {
    .admin-reply-page {
      padding: 10px;
    }
  
    table {
      font-size: 0.9rem;
    }
  
    th, td {
      padding: 12px;
    }
  
    textarea {
      min-height: 80px;
    }
  }
  