@import "./variables.scss";
@import "./breakpoints.scss";

.payment-cancel-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2rem;
    text-align: center;
    max-width: 600px;
    margin: 0 auto;
    font-family: nunito, sans-serif;
    
    h1 {
      font-size: 2.5rem;
      color: $pinkred; /* Red color for error */
      margin-bottom: 1rem;
    }
  
    p {
      font-size: 1.2rem;
      color: #333;
      margin-bottom: 2rem;
    }
  
    .go-home-btn {
      background-color: $blue;
      color: #fff;
      border: none;
      padding: 1rem 2rem;
      font-size: 1.2rem;
      cursor: pointer;
      border-radius: 5px;
      transition: background-color 0.3s;
  
      &:hover {
        background-color: $blue;
      }
    }
  }
  